@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&display=swap);
body {
  display: flex;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  background-color: #F0F0F0;
}
#root{
  overflow-x: auto;
}

.App{
  display: flex;
  max-width: 1240px;
  min-width: 1240px;
  justify-content: space-between;
  margin: 0 auto;
  border-top: 36px solid #11adb5;
}
.pdf-generator{
  display: none;
}
.redactor-buttons{
  display: flex;
  justify-content: space-between;
  width: 50%;
}
.save-button {
  display: block;
  background: none;
  border: 1px solid #11adb5;
  margin-top: 10px;
  padding: 5px;
}
.saveButton{
  display: block;
}

/* AVATAR */
.header {
  display: flex;
  flex-direction: column;
  background-color: #f7f7f7;
  width: 52%;
  padding-top: 22px;
}
.ava-img {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.avatar-container {
  border-radius: 100%;
  border: 6px solid #11adb5;
  width: 180px;
  height: 180px;
  overflow: hidden;
}
.avatar{
  width: 180px;
}
.browse-img {
  background: none;
}
.input {
  background: none;
  border: none;
  border-bottom: 1px solid #11adb5;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  outline: 0;
}
/* BUTTONS STYLES */
.add-buttons{
  margin-top: 15px;
  width: 100% !important;
  margin-bottom: 7px;
}
.add-buttons button{
  background: none;
  border: 1px solid #11adb5;
  width: 100px;
}  
.add-buttons button:nth-child(2){
  background: none;
  border: 1px solid #11adb5;  
  width: auto;
  height: 30px;
  margin: 0 auto;
}  
.add-buttons select{
  background: none;
  border: 1px solid #11adb5; 
}  
.add-element{
  display: flex;
  width: 100% !important;
}
.add-element select{
  width: 50%;
}
.img-options{
  width: 80px;
}
/* CONTENT */
.skills{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  flex: 1 1;
}
.skills div{
  display: flex;
}
.code {
  margin: 0 auto;
  height: 560px;
}
.code img{
  display: flex;
  width: 340px;
  height: 340px;
  margin: 100px 0 70px 0;
}
/* ADDED ELEMENTS */
.input-name{
  text-align: center;
  font-weight: 700;
  font-size: 52px;
  width: 100%;
  margin-top: 16px;
}
.input-profession{
  text-align: center;
  margin-top: -2px;
  width: 300px;
  font-size: 31px;
  color: #11adb5 !important;
}
.header-container{
  display: flex;
  align-items: flex-start;
  margin: 60px 0 12px 20px;
  /* margin-bottom: 12px; */
}
.header-input{
  font-size: 24px;
  width: 260px;
  margin-top: 8px;
  padding-left: 14px;
  text-transform: uppercase;
}
.icon-column{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #11adb5;
  padding: 9px;
  border-radius: 100%;
  width: 25px;
  height: 25px;
}
.language{
  width: 25px;
}
.skill-Dev {
  padding-left: 3px;
  padding-bottom: 2px;
  width: 32px;
  height: 32px;
}
.column-input{
  font-family: 'Montserrat', sans-serif;
  font-size: 17px;
  font-weight: 600;
  margin-top: 12px;
  margin-left: 30px;
  margin-bottom: 3px;
  width: 302px;
}
.column-input-mpTop {
  margin-top: 20px;
}
.progress-bar{
  display: flex;
  align-items: baseline;
  width: 335px;
}
.progress-input{
  font-weight: 400;
}
.progress-bar{
  margin: 1px;
}
.progress-bar div:first-child{
  display: flex;
  flex-direction: column;
}
.progress-bar .textarea{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: -0.5px;
}
.pBar-border{
  width: 312px;
  border: 1px solid #11adb5;
}
.pBar-progress{
  background-color: #11adb5;
  height: 5px;
  width: 20%;
  margin: 0px !important;
}
.procents{
  background: none;
  border: none;
  border-bottom: 1px solid #11adb5;
}
.marker {
  display: flex;
  align-items: flex-start;
  list-style: none; 
  width: 340px;
  margin-left: 0px;
  margin-bottom: -5px;
}
.marker::before {
  content: "\2022"; 
  color: #11adb5;
  font-weight: bold;
  width: 1em;
  height: 1em;
  font-size: 34px;
  margin-right: 10px;
}
.marker-input{
  font-family: 'Montserrat', sans-serif;
  font-size: 17px;
  font-weight: 400;
  width: 313px;
  max-width: 340px;
}
.bio {
    width: 100%;
    background-color: white;
}
.contacts{
    display: flex;
    justify-content: space-between;
    padding: 15px 0 7px 0;
    border-bottom: 2px solid #6ecdd2;
    padding-left: 3%;
}
.contact-info {
    display: flex;
    align-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}
.contact-info .info{
  display: flex;
  align-items: flex-start;
  margin: 10px;
}
.contact-info .info div{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 10px;
    height: 25px;
}
.info svg{
    font-size: 19px;
    max-width: 14px;
}
.info path{
   max-width: 1px;
}
.info span {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 17px;
}
.info input{
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 18px;
    height: 20px;
    width: 320px;
}
.logo{
    margin-right: 6%;
    padding-top: 35px;
}
.logo img{
    width: 310px;
}
.logo h2{
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    margin-top: 10px;
    font-size: 32.6px;
}
/* BUTTONS */
.bio-element button{
    width: 200px !important;
}
.bio-buttons button:nth-child(3){
    width: 200px !important;
    height: 30px;
}
/* STYLES TO ADDED ELEMENTS */
.bio-header-container{
    display: flex;
    margin-top: 28px;
    margin-left: -22px;
}
.bio-header-container:not(:first-child){
   margin-top: 30px
}
.bio-header-area{
    font-size: 24px;
    font-weight: 600 !important;
    width: 100%;
    margin-left: 15px;
}
.resume{
  height: 30px;
  width: 30px;
  padding-top: 2px;
  padding-left: 5px;
}
.experience {
  padding-top: 1px;
  padding-left: 6px;
  width: 31px;
  height: 31px;
}
.mortarboard {
    width: 30px;
    height: 30px;
}
.onlineCourse {
    padding-left: 2px;
    width: 26px;
    height: 26px;
}
.button-img {
    padding-top: 1px;
    padding-left: 0px;
    width: 31px;
    height: 31px;
}
.briefing {
    padding-top: -3px;
    padding-left: 5px;
    width: 31px;
    height: 31px;
}
@-moz-document url-prefix() {
  .bio-marker {
    margin-left: -11px !important;
    align-items: baseline !important;
  }
}
.bio-marker-container{
    display: flex;
    align-items: baseline;
    width: 100%;
}
.bio-marker {
    display: flex;
    align-items: flex-start;
    list-style: none;
    margin: 0px 0px 10px -6px; 
}
.bio-marker::before {
    content: "\2022"; 
    color: #11adb5;
    font-size: 34px;
    font-weight: bold;
    padding-right: 12px;
    width: 1em;
    height: 1em;
}
.bio-marker-input{
    font-size: 18px;
    width: 100%;
}
.bio-marker-area{
    align-items: center;
    font-size: 18px;
}
.bio-area-marker{
    display: flex;
    align-items: flex-start;
    margin-left: 37px;
    list-style: none; 
    width: 96%;
}
li .bio-area-container {
  width: 90.6%;
}
.bio-area-marker::before {
    content: "\2022"; 
    color: #11adb5;
    font-size: 34px;
    font-weight: bold;
    width: 24px;
}
.bio-area-container {
    display: flex;
    width: 93.7%;
}
.bio-container{
    display: flex;
    margin-left: 36px;
    margin-bottom: -8px;
    width: 95.2%;
}
.bio-container-mg{
    margin-top: -8px;
    margin-bottom: 13px;
    margin-left: 47px;
}
.textarea {
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
    border: none;
    border-bottom: 1px solid #6ecdd2;
    outline: none;
    background: none;
}
.contint {
    display: block !important;
    font-family: 'Montserrat', sans-serif; 
    color: rgb(37, 37, 37);
    margin-top: 7px;
    overflow: hidden;
    outline: none;
}
span {
    color: black;
}
.marker .contint {
    margin-top: 7px;
}
.marker .textarea {
    width: 100%;
    margin-top: 7px;
}
.bio-area-container .textarea{
    margin-top: 7px;
    width: 100%;
}
.bio-marker .textarea {
    margin-top: 7px;
}
.bio-container-mpTop {
    margin-top: 40px;
}
.close-icon{
    display: flex;
    /* flex-direction: column; */
    /* margin-left: 4px; */
    color: rgb(179, 179, 179);
}
.close-icon span{
    color: rgb(179, 179, 179);
}
.close-icon span:nth-child(1){
    margin-right: 6px;
}
.close-icon span svg{
    pointer-events: none;
}
/* CONTENT FIELD */
.biography {
    border-left: 1px solid #11adb5;
    margin: 0 0 40px 65px;
    width: 90.8%;
}

